body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-content{
    width: 600px;
    margin-top: 50px;
    padding:40px;
    background-color: #B9FFFD  ;
    margin-bottom: 50px;
}

@media(max-width: 650px){
    .app-content{
        margin:20px 20px 0;
        width: auto;
    }
}

.center-block {
    display: block;
    margin-right: auto;
    margin-left: auto;
  }

  .text-center{
      
      display: block;
      margin-right: auto;
      margin-left: auto;
      text-align: center
  }
  h3{
      font-weight: 200;
  }
  h1{
      font-weight: 250;
    }
    h2{
        text-align: center;
    }
  .form-control{
      width: 70px;
   
      padding-left: 25px;
  }
  
  .form-group{
      padding-left: 110px;
  }
  
